.background-video {
  position: fixed;
  right: 0;
  left: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  filter: blur(5px);
  z-index: -1;
}

.contact {
  background-color: black;
  text-align: center;
  color: white;
  padding: 0.2em 0 2.5em 0;
}

.home {
  display: flex;
  text-align: center;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.logo {
  margin: 2em 0 1em 0;
}

.brief {
  margin: 1em 0 1.5em 0;
  font-size: 2em;
  color: white;
  text-shadow:
    2px 2px 0 #000,
   -2px -2px 0 #000,  
    2px -2px 0 #000,
   -2px 2px 0 #000,
    2px 2px 0 #000;
    font-size: 1.5em;
}

.bio {
  margin: 1em 0 3em 0;
  background-color: black;
  border-style: solid;
  border-width: 0.07em;
  border-radius: 0.3em;
  padding: 0.5em;
  color: white;
}

.bio-text {
  margin: 0.5em 1em 1.5em 1em;
}

.bio-links {
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;
  margin: 0.5em 1em 0.5em 1em;
}

.projects-section {
  display: flex;
  margin: 0;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.8);

  border-top: white;
  border-bottom: white;
  border-style: solid;
  border-width: 0.05em;
  
  overflow: hidden;
  padding: 1em 1em 1em 1em;
}

.title-section {
  color: white;
  font-size: 2em;
  margin: 0em 0 0.5em 0;
  text-align: center;
}

.img-scroll-container {
  display: flex;
  width: 100%;
  flex-direction: row;
  overflow: auto;
  padding-bottom: 1em;
}

.img-scroll-container img{
  height: 200px;
  width: 390px;
  border-style: solid;
  border-width: 0.07em;
  border-radius: 1.5em;
  color: white;
}

.img-scroll-container iframe{
  height: 220px;
  width: 390px;
  min-width: 390px;
  margin-right: 1.2em;
  margin-left: 0.7em;
  border-style: solid;
  border-width: 0.07em;
  border-radius: 1.5em;
  color: white;
}

.audio-content audio {
  padding-top: 10px;
}

.audio-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0em 1em 0em 1em;
}